import { useEffect, useState, useRef } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import Breadcrumbs from "../../../components/Breadcrumbs";
import ListTitle from "../../../components/ListTitle";
import { Table, Popover, Popconfirm } from "antd";
import { useFetchByLoad } from "../../../contexts/useFetchByLoad";
import { useDelete } from "../../../contexts/useDelete";
const resource = "system_emails_templates";

export default function EmailTemplates() {
	const navigate = useNavigate();
	let timer = useRef<any>(null)
	const [query, setQuery] = useState({ "skip": 0, "take": 10, search: '' })
	const { remove, data: dataRemove, loading: loadingRemove } = useDelete()
	const { fetch, data, loading } = useFetchByLoad();

	useEffect(() => {
		clearTimeout(timer.current)
		timer.current = setTimeout(() => {
			fetch({ url: resource, query: JSON.stringify(query) })
		}, 500)
	}, [query, dataRemove])

	const info = (records: any) => {
		return <>
			<p>Created On: 07-Nov-2022 at 18:09 PM<br />
				Created By: Instrowest Pty Ltd (apps@instrowest.com.au)</p>

			<p>Modified On: 11-Nov-2022 at 18:09 PM<br />
				Modified By: Instrowest Pty Ltd (apps@instrowest.com.au)</p>
		</>
	}
	const columns = [
		{
			title: "Application Name",
			dataIndex: "systemAppId",
			sorter: true,
		},
		{
			title: "SMTP Server Name",
			dataIndex: "systemSmtpServerId",
			sorter: true,
		},
		{
			title: "Email Template Title",
			dataIndex: "systemEmailTemplateTitle",
			sorter: true,
		},
		{
			title: "Email Template Action",
			dataIndex: "systemEmailAction",
			sorter: true,
		},
		{
			title: "Actions",
			dataIndex: "actions",
			key: "actions",
			render: (_value: any, record: any) => (
				<div className="text-center actions">
					<NavLink to={`/edit-email-template/${record.id}`} title="Edit Email Template"><img className="navigicon" src="assets/apps/images/edit-dark-grey.png" /></NavLink>&nbsp;&nbsp;
					<Popconfirm
						title="Delete the email template"
						description="Are you sure to delete this email template?"
						onConfirm={() => remove({ url: resource, body: record })}
						okText="Yes"
						cancelText="No"
					><img className="navigicon" src="assets/apps/images/lock-dark-grey.png" />
					</Popconfirm>&nbsp;&nbsp;
					<Popover content={info(record)} title="" trigger="click" className="recloginfo"><img className="navigicon" src="assets/apps/images/info-dark-grey.png" /></Popover>
				</div>
			),
		},
	];

	return (
		<>
			<Breadcrumbs root="" pageName="Email Templates" />

			<div className="main-content-body">
				<div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
					<div className="row">
						<div className="card">
							<ListTitle ListName="List All Email Templates" />
							<div className="card-body">
								<div className="dataTables_wrapper form-inline dt-bootstrap no-footer">
									<div className="clear"></div>
									<div className="col-lg-6 col-md-6 col-sm-6 col-xs-12 no-padding dataTables_length">
										<label>
											<select className="fl form-control input-sm"
												value={query.take} onChange={(obj) => setQuery({ ...query, "take": Number(obj.target.value) })}>
												<option value="10">10</option>
												<option value="20">20</option>
												<option value="25">25</option>
												<option value="50">50</option>
												<option value="100">100</option>
											</select>
											<div className="fl rec">records per page.</div>
										</label>
									</div>
									<div className="dataTables_filter">
										<label>Search Record: <input type="search" className="form-control input-sm" placeholder="" value={query.search} onChange={(obj) => setQuery({ ...query, "search": obj.target.value })} /></label>
									</div>
								</div>
								<Table className="mainTable" loading={loading || loadingRemove} dataSource={data?.data ?? []} columns={columns} pagination={{
									showQuickJumper: true,
									total: data?.count ?? 0,
									showTotal: (total) => `Showing ${total} records.`,
									onChange: (page, pageSize) => {
										setQuery({ ...query, "skip": ((page - 1) * pageSize), "take": pageSize });
									},
								}} />

								<div className="clear"></div>

								<div className="card-action table-buttons">
									<div className="fl">
										<button type="button" onClick={() => navigate('/add-email-template')} className="btn btn-success add btn-raised text-uppercase ripple">Add New Email Template</button>
									</div>
								</div>

								<div className="clear"></div>

							</div>
						</div>
					</div>
				</div>
			</div>


		</>
	);
}
